<template>
  <Loading :active="isLoading"></Loading>
  <div class="wrap">
    <div class="container">
      <h2></h2>
    <div class="stepper">
      <ul>
        <li >Step 1 <br></li>
        <div class="arrowIconRight">
          <font-awesome-icon  :icon="['fas', 'arrow-right']"/>
        </div>
        <div class="arrowIconDown">
          <font-awesome-icon  :icon="['fas', 'arrow-down']"/>
        </div>
        <li >Step 2 <br></li>
        <div class="arrowIconRight">
          <font-awesome-icon  :icon="['fas', 'arrow-right']"/>
        </div>
        <div class="arrowIconDown">
          <font-awesome-icon  :icon="['fas', 'arrow-down']"/>
        </div>
        <li>Step 3 <br></li>
      </ul>
    </div>
    <div class="mt-5 row justify-content-center">
    <Form class="col-md-6" v-slot="{ errors }" @submit="createOrder">
      <div class="mb-3 w-100">
        <label for="email" class="form-label text-control">Email:</label>
        <Field id="email" name="email" type="email" placeholder=" Email" class="form-control" :class="{ 'is-invalid': errors['email'] }" rules="email|required" v-model="form.user.email"></Field>
        <ErrorMessage name="" class="invalid-feedback"></ErrorMessage>
      </div>
      <div class="mb-3">
        <label for="name" class="form-label text-control">:</label>
        <Field id="name" name="" type="text" placeholder=" " class="form-control" :class="{ 'is-invalid': errors[''] }" rules="required" v-model="form.user.name"></Field>
        <ErrorMessage name="" class="invalid-feedback"></ErrorMessage>
      </div>
      <div class="mb-3">
        <label for="tel" class="form-label text-control">:</label>
        <Field id="tel" name="" type="tel" placeholder=" " class="form-control" :class="{ 'is-invalid': errors[''] }" rules="required" v-model="form.user.tel"></Field>
        <ErrorMessage name="" class="invalid-feedback"></ErrorMessage>
      </div>
      <div class="mb-3">
        <label for="address" class="form-label text-control">:</label>
        <Field id="address" name="" type="text" placeholder=" " class="form-control" :class="{ 'is-invalid': errors[''] }" rules="required" v-model="form.user.address"></Field>
        <ErrorMessage name="" class="invalid-feedback"></ErrorMessage>
      </div>
      <div class="mb-3">
        <label for="message" class="form-label text-control"></label>
        <textarea name="" id="message" class="form-control" cols="30" rows="10" v-model="form.message"></textarea>
      </div>
      <div class="linkBtn">
        <router-link class="btn btn-outline-secondary linkBtn__returnProduct" to="/cart/cartPage"></router-link>
        <button class="btn btn-danger linkBtn__goCheck"></button>
      </div>
    </Form>
  </div>
  </div>
  </div>
</template>

<script>
export default {
  name: 'UserCheck',
  data () {
    return {
      form: {
        user: {
          name: '',
          email: '',
          tel: '',
          address: ''
        },
        message: ''
      },
      isLoading: false
    }
  },
  methods: {
    createOrder () {
      const postOrder = `${process.env.VUE_APP_API}api/${process.env.VUE_APP_PATH}/order`
      this.isLoading = false
      const order = this.form
      this.$http.post(postOrder, { data: order })
        .then((res) => {
          // console.log(res)
          this.isLoading = false
          const orderId = res.data.orderId
          this.$router.push(`./order/${orderId}`)
        })
    }
  }
}
</script>

<style lang="scss" scoped>
@import "src/assets/css/cartCss/userCheck";
@import "src/assets/css/cartCss/stepper";
@import "src/assets/css/cartCss/sameCssForCart";
</style>
